

/* Default styles */
body {
    font-family: poppins;
}

.navbar {
    display: flex;
    justify-content: space-around;
    padding: 10px;
    box-shadow: 0 1px 3px -2px black;
    align-items: center;
}

.nav-logo {
    display: flex;
    align-items: center;
    gap: 10px;
}

.nav-logo p {
    color: #171717;
    font-size: 35px;
    font-weight: 600;
}

.nav-menu {
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: #626262;
    font-size: 20px;
}

.nav-menu li {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}

.nav-menu hr {
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background: #ff4141;
}

.nav-login-cart {
    display: flex;
    align-items: center;
    gap: 45px;
}

.nav-login-cart button {
    width: 150px;
    height: 55px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color: #515151;
    font-size: 20px;
    font-weight: 500;
    background-color: white;
    cursor: pointer;
}

.nav-login-cart button:active {
    background-color: #f3f3f3;
}

.nav-cart-count {
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -55px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background: red;
    color: white;
}
.nav-dropdown{
width: 50px;
display: none;
}

 @media(max-width:1280px){
    .navbar{
        padding: 12px 50px;
    }
    .nav-logo img{
        width: 40px;
    }
    .nav-logo p{
        font-size: 25px;
    }
    .nav-menu{
        gap: 30px;
        font-size: 16px;
    }
    .nav-login-cart button{
        width: 120px;
        height: 45px;
        font-size: 16px;
    }
    .nav-cart-count{
        margin-left: -40px;
        font-size: 12px;
    }
 }
.navbar-toggler-icon{
    color: orange;
}
.navbar-collapse{
    color: red;
}
 
 @media(max-width:1024px){
    .navbar{
        padding: 12px 30px;

    }
    .nav-menu{
        gap: 25px;
        font-size: 14px;
    }
    .nav-login-cart button{
        width: 80px;
        height: 35px;
        font-size: 14px;
    }
    .nav-cart-count img{
      width: 30px;
    }
    .nav-cart-count{
        width:18px ;
        height: 18px;
    }
 }
 @media(max-width:800px){
    .nav-menu {
        display: none;
        flex-direction: column; /* Adjust the direction according to your layout */
        position: absolute;
        top: 50px;
        left: 0; /* Adjust the positioning according to your layout */
        background-color: white;
        padding: 10px; /* Add padding or adjust as needed */
      }
      
      .nav-menu-visible {
        display: flex;
      }
      
      .nav-dropdown {
        width: 30px;
        transition: transform 0.5s;
      }
      
      .nav-dropdown.open {
        transform: rotate(90deg);
      }
      .login-btn{
        display: none;
      }

 }