.productdisplay{
    display: flex;
    margin: 0px 170px;
   
  
}
.productdisplay-left{
    display: flex;
    gap: 17px;
}
.productdisplay-img-list{
    display: flex;
    flex-direction: column;
    gap: 16px;

}
.productdisplay-img-list img{
    height: 163px;
}
.productdisplay-main-img{
    width: 586px;
    height: 700px;
}
.productdisplay-right{
    margin: 0px 70px;
    display: flex;
    flex-direction: column;
}
.productdisplay-right h1{
    color: #3d3d3d;
    font-size: 40px;
    font-weight: 700;
}
.productdisplay-right-star{
    display: flex;
    align-items: center;
    margin-top: 13px;
    gap: 5px;
    color: #1c1c1c;
    font-size: 16px;
}
.productdisplay-right-star p{
    display: flex;
    align-items: center;
    justify-content: center;
}
.productdisplay-right-prices{
    display: flex;
    margin: 40px 0px;
    gap: 30px;
    font-size: 24px;
    font-weight: 700;
}
.productdisplay-right-price-old{
    color: #818181;
    text-decoration: line-through;
}
.productdisplay-right-price-new{
    color: #ff4141;
}
.productdisplay-right-size h1{
    margin-top: 55px;
    color: #656565;
    font-size: 20px;
    font-weight: 600;
}
.productdisplay-right-sizes{
    display: flex;
    margin: 30px 0px;
    gap: 20px;
}
.productdisplay-right-sizes div{
padding: 18px 24px;
background-color: #fbfbfb;
border: 1px solid #ebebeb;
border-radius: 3px;
cursor: pointer;

}
.productdisplay-right button{
    padding: 20px 40px;
    width: 200px;
    font-size: 16px;
    font-weight: 600;
    color: white;
    background-color: #ff4141;
    margin-bottom: 40px;
    border: none;
    outline: none;
    cursor: pointer;

}
.productdisplay-right-category{
    margin-top: 10px;
}
.productdisplay-right-category span{
    font-weight: 600;
}


@media(max-width:500px){
    .productdisplay-img-list{
        display: none;
    }
    .productdisplay-main-img{
        width: 350px;
        height: 400px;
    }
    .productdisplay{
        display: flex;
        margin: 0px 10px;
       
        padding-right: 30px;
     
       
      
    }
    .productdisplay-img-list img{
        height: 100px;
    }
    .productdisplay-right-sizes{
      
        display: flex;
        flex-wrap: wrap;
        margin: 10px 0px;
        gap: 5px;
    }
    .productdisplay-right-sizes div{
        padding: 18px 24px;
        background-color: #ede5e5;
        border: 1px solid #eae3e3;
        border-radius: 3px;
        cursor: pointer;
        
        }
        .cart-btn{
            width: 100%;
        }
}