.newsletter{
    width: 95%;
    height: 40vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    padding:  0 130px;
    margin-bottom: 150px;
    background: linear-gradient(180deg, #fde1ff 0% ,#e1ffea22 60%);
    gap: 30px;

}

.newsletter h1{
    color: #454545;
    padding-top: 45px;
    font-size: 30px;
    font-weight: 600;
}
.newsletter p{
    color: #454545;
    font-size: 15px;

}
.newsletter div{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: white;
    width: 732px;
    height: 70px;
    border-radius: 80px;
    border: 1px solid black;
}

.newsletter input{
    width:500px;
    margin-left: 30px;
    border: none;
    outline: none;
    font-family: Poppins;
    font-size: 16px;
}
.newsletter button{
    width: 210px;
    height: 70px;
    border-radius: 80px;
    background: black;
    color: white;
    font-size: 16px;
    cursor: pointer;
}
@media(max-width:800px){
    .newsletter{
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: auto;
        padding:  0 4px;
        margin-bottom: 150px;
        background: linear-gradient(180deg, #fde1ff 0% ,#e1ffea22 60%);
        gap: 30px;
    
    }
    .newsletter h1{
        color: #454545;
        padding-top: 25px;
        font-size: 30px;
        font-weight: 500;
        padding: 0 20px;
    }
   
    .newsletter p{
        color: #454545;
        font-size: 18px;
        padding: 0 20px;
    
    }
    .newsletter div{
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: white;
        width: 320px;
        height: 40px;
        border-radius: 80px;
        border: 1px solid black;
    }
    .newsletter input{
        width:200px;
        height: 30px;
        margin-left: 30px;
        border: none;
        outline: none;
        font-family: Poppins;
        font-size: 16px;
    }
    .newsletter button{
        width: 100px;
        height: 40px;
        border-radius: 80px;
        background: black;
        color: white;
        font-size: 16px;
        cursor: pointer;
        border: none;
    }
}
@media(max-width:500px){
    .newsletter input{
        width:150px;
        height: 30px;
        margin-left: 30px;
        border: none;
        outline: none;
        font-family: Poppins;
        font-size: 16px;
    }
}