.offers{
    margin-top: 50px;
    padding-top: 50px;
    width: 80%;
    height: 60vh;
    display: flex;
    margin: auto;
    padding: 0px 140px ;
    margin-bottom:150px ;
    background: linear-gradient(180deg,#fde1ff 0%,#e1ffea22 60%);
}
.offers-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.offers-left h1{
    color: #171717;
    font-size: 50px;
    font-weight: 600;

}
.offers-left p{
    color: #171717;
    font-size: 22px;
    font-weight: 600;
}
.offers-left button{
    width: 282px;
    height: 70px;
    border-radius: 35px;
    background: #ff4141;
    border: none;
    color: white;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
}
.offers-right{
    flex: 1;
    display: flex;
    /* align-items: center; */
    justify-content: flex-end;
    padding-top: 50px;
}
@media(max-width:800px){
    .offers{
        width: 100%;
        display: flex;
        margin: auto;
        padding: 0px 25px ;
        margin-bottom:150px ;
        background: linear-gradient(180deg,#fde1ff 0%,#e1ffea22 60%);
    }
    .offers-left h1{
        color: #171717;
        font-size: 40px;
        font-weight: 600;
    
    }
    .offers-left p{
        color: #171717;
        font-size: 14px;
        font-weight: 600;
    }
    .offers-left button{
        width: 200px;
        height: 60px;
        /* border-radius: 35px; */
        background: #ff4141;
        border: none;
        color: white;
        font-size: 16px;
        font-weight: 500;
        margin-top: 20px;
        cursor: pointer;
    }
    .offers-right img{
        display: none;
    }
}